import React, { Component } from 'react';

class RowDetailsMoviesShowing extends Component {
  render() {
    const details = this.props.details;
    let format = details.format;
    if (format.match(/^Regular/)) {
      format = 'Regular';
    } else if (format.match(/^In 3D/)) {
      format = '3D';
    } else if (format.match(/D-Box/)) {
      format = 'AVX + 3D + D-box';
    } else if (format.match(/AVX/)) {
      format = 'AVX + 3D';
    } else if (format.match(/VIP.*?3D/)) {
      format = 'VIP + 3D';
    } else if (format.match(/VIP/)) {
      format = 'VIP';
    }
    const times = details.today.replace(/Today\s\([^)]*\)/gi, '').trim();
    return (
      <div className="movie__time">
        <div>{format}</div>
        <div>{times}</div>
      </div>
    );
  }
}

export default RowDetailsMoviesShowing;



