import React, { Component } from 'react';
import './RowDetailsMovies.css';
import RowDetailsMoviesShowing from './RowDetailsMoviesShowing';

class RowDetailsMovies extends Component {
  render() {
    const details = this.props.details;
    return (
      <div className="movie">
        <div className="movie__header">
          <div className="movie__img"><img src={details.imgSrc} alt=""/></div>
          <div className="movie__details">
            <div className="movie__title">{details.title}</div>
            <div className="movie__genre">{details.genre}</div>
          </div>
        </div>
        <div>
          {
            Object
              .keys(details.showings)
              .map(key => <RowDetailsMoviesShowing key={key} index={key} details={details.showings[key]}/>)
          }
        </div>
      </div>
    );
  }
}

export default RowDetailsMovies;



