import React, { Component } from 'react';
import './Row.css';
import RowDetailsWeather from './RowDetailsWeather';
import RowDetailsRiders from './RowDetailsRiders';
import RowDetailsMovies from './RowDetailsMovies';
import RowDetailsGas from './RowDetailsGas';

class Row extends Component {
  constructor(props) {
    super(props)
    this.state = {
      expanded: false,
    };
    this.toggleExpanded = this.toggleExpanded.bind(this);
  }
  toggleExpanded() {
    this.setState({ expanded: !this.state.expanded });
  }
  componentDidMount() {
    // var $this = $(ReactDOM.findDOMNode(this));
    // inlineSVG.init({
    //   svgSelector: 'img.inlineSvg', // the class attached to all images that should be inlined
    //   initClass: 'js-inlinesvg', // class added to <html>
    // }, function () {
    //   console.log('All SVGs inlined');
    // });
  }
  render() {
    return (
      <div className={`row ${this.state.expanded ? 'row--expanded' : ''}`}>
        <div className="row-header" onClick={this.toggleExpanded}>
          <div className="row-header-left">
            <div className="row-header-title">
              {this.props.title}
            </div>
            <div className="row-header-value-1">
              {this.props.value1}
            </div>
            <div className="row-header-value-2">
              {this.props.value2}
            </div>
          </div>
          <div className="row-header-right">
            <div className="row-header-icon">
              <img src={this.props.icon} className={this.props.iconInline ? 'inlineSvg' : ''} alt=""/>
            </div>
          </div>
        </div>
        {this.props.showBody &&
          <div className="row-body">
            <div style={{lineHeight: 1.4}}>The first Thanksgiving by Europeans in North America was held by Sir Martin Frobisher and his crew in the Eastern Arctic in 1578. They ate a meal of salt beef, biscuits and mushy peas to celebrate and give thanks for their safe arrival in Newfoundland.</div>
          </div>
        }

        {this.props.weatherItems &&
        <div className="row-body">
            <table className="table">
              <thead>
              <th>Date</th>
              <th>Temp</th>
              <th>Condition</th>
              </thead>
              <tbody>
              {
                Object
                  .keys(this.props.weatherItems)
                  .map(key => <RowDetailsWeather key={key} index={key} details={this.props.weatherItems[key]}/>)
              }
              </tbody>
            </table>
          </div>
        }

        {this.props.gasPrices &&
        <div className="row-body">
          {
            Object
              .keys(this.props.gasPrices)
              .map(key => <RowDetailsGas key={key} index={key} details={this.props.gasPrices[key]}/>)
          }
        </div>
        }

        {this.props.movies &&
        <div className="row-body">
          {
            Object
              .keys(this.props.movies)
              .map(key => <RowDetailsMovies key={key} index={key} details={this.props.movies[key]}/>)
          }
        </div>
        }

        {this.props.riders &&
        <div className="row-body">
          <table className="table table--small">
            <thead>
            <th>Date</th>
            <th>State</th>
            <th colspan="2">Away</th>
            <th colspan="2">Home</th>
            </thead>
            <tbody>
            {
              Object
                .keys(this.props.riders)
                .map(key => <RowDetailsRiders key={key} index={key} details={this.props.riders[key]}/>)
            }
            </tbody>
          </table>
        </div>
        }

        {this.props.sourceText &&
          <div className="row-footer">
            <b>Source:</b> <a target="_blank" href={this.props.sourceHref}>{this.props.sourceText}</a>
          </div>
        }
      </div>
    );
  }
}

export default Row;



