import React, { Component } from 'react';
import moment from 'moment';

class RowDetailsWeather extends Component {
  render() {
    const details = this.props.details;
    return (
      <tr>
        <td className="text-right no-wrap">{moment(details.date).format('MMM D h:mm a')}</td>
        <td className="text-center">{details.state}</td>
        <td>{details.awayTeam}</td>
        <td className="text-right">{details.awayScore}</td>
        <td>{details.homeTeam}</td>
        <td className="text-right">{details.homeScore}</td>
      </tr>
    );
  }
}

export default RowDetailsWeather;



