import React, { Component } from 'react';
import moment from 'moment';

class RowDetailsWeather extends Component {
  render() {
    return (
      <tr>
        <td className="text-right">{moment(this.props.details.date).format('ddd hh:mm a')}</td>
        <td className="text-right">{this.props.details.temperature}</td>
        <td>{this.props.details.condition}</td>
      </tr>
    );
  }
}

export default RowDetailsWeather;



