import React, { Component } from 'react';
import './RowDetailsGas.css';

class RowDetailsGas extends Component {
  render() {
    const details = this.props.details;
    return (
      <div className="gas">
        <div className="gas__price">
          <div>{details.price}</div>
        </div>
        <div className="gas__station">
          <div className="gas__station-title">{details.station}</div>
          <div className="gas__station-address">{details.location}</div>
        </div>
        <div className="gas__area">
          {details.area}
        </div>
      </div>
    );
  }
}

export default RowDetailsGas;



